.App {
  width: 80%;
  margin: 0 auto;
  box-sizing: border-box;
}

.App .App-link {
  color: #747caa;
  text-decoration: none;
}

.App .App-head {
  width: 100%;
  margin-top: 10px;
  border: solid 3px #88c7e6;
  box-sizing: border-box;
}

.App .App-head .head-box-a {
  padding: 10px;
  display: flex;
  align-items: center;
  background: #e2eced;
  font-size: 12px;
  color: #555555;
  border-bottom: solid 1px #88c7e6;
}

.App .App-head .head-box-b {
  padding: 10px;
}

.App .App-head .head-box-b .head-title {
  color: #555555;
  font-size: 30px;
  font-weight: 600;
}

.App .App-head .head-box-b .head-affx {
  color: #444444;
  font-size: 15px;
  font-weight: 400;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: space-evenly;
  border-bottom: dashed 1px #88c7e6;
  padding-bottom: 10px;
}

.App .App-head .head-box-b .head-desc {
  margin-top: 10px;
  color: #444444;
  font-size: 14px;
}

.App .App-list {
  width: 100%;
  padding: 2px;
  margin-top: 10px;
  border: solid 3px #88c7e6;
  box-sizing: border-box;
}

.App .App-list .list-head {
  background: #C3DFEB;
  padding: 10px;
  font-size: 14px;
  color: #555555;
  display: flex;
  justify-content: center;
  align-items: center;
}

.App .App-list .list-main {
  list-style: none;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  justify-items: flex-start;
  padding: 0;
}

.App .App-list .list-main li {
  width: 33%;
  box-sizing: border-box;
  border-bottom: dashed 1px #CCCCCC;
  padding: 5px;
  font-size: 14px;
}

@media screen and (max-width: 768px) {
  .App {
    width: 95%;
  }
  .App .App-list .list-main li {
    width: 100%;
  }
}
