.SearchHeader {
  display: flex;
  margin-top: 20px;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-between;
}

.SearchHeader .logo {
  border: double 10px #ce0000;
  font-size: 20px;
  color: #ce0000;
  padding: 5px 10px;
  display: flex;
  font-family: OCR A Std, monospace;
  justify-content: center;
  align-items: center;
}

.SearchHeader .search-v {
  border: solid 1px #d7d7d7;
  height: 35px;
  width: 400px;
}

.SearchHeader .search-v form {
  height: 100%;
  display: flex;
  justify-content: space-between;
}

.SearchHeader .search-v form input {
  outline: none;
  height: 100%;
  width: 70%;
  border: none;
  padding: 5px;
  box-sizing: border-box;
}

.SearchHeader .search-v form input[type="submit"] {
  background: #f5f5f5;
  height: 100%;
  width: 30%;
  display: inline-block;
  color: #b2b2b2;
  outline: none;
  border-left: solid 1px #d7d7d7;
  cursor: pointer;
}

@media screen and (max-width: 768px) {
  .SearchHeader {
    justify-content: center;
  }
  .SearchHeader .search-v {
    margin-top: 10px;
    width: 100%;
  }
}
